import { Injectable } from '@angular/core';
import { Http, Headers} from '@angular/http';
import 'rxjs/add/operator/map';

@Injectable()
export class ChatService {

  constructor(private http: Http) {
  }

  getChatInfo(){
    const headers = new Headers();
    headers.append('Content-Type', 'application/json');
    return this.http.get('chats/getInfo', {headers: headers})
      .map(res => res.json());
  }

  sendChatMessage(nameObj: string, usernameObj: string, messageObj: string, timeZoneObj: string, utcStringObj: string) {
    const headers = new Headers();
    const chat = {
      name: nameObj,
      username: usernameObj,
      message: messageObj,
      timeZone: timeZoneObj,
      utcString: utcStringObj
    }
    headers.append('Content-Type', 'application/json');
    return this.http.post('chats/postChat', chat, {headers: headers})
      .map(res => res.json());
  }
}



